@import "variables.scss";
.our-mentors-block {
  margin-bottom: 100px;
  .container {
    text-align: center;
    .slick-slider {
      ul {
        li {
          button {
            &:before {
              font-family: "slick";
              font-size: 13px;
              line-height: 20px;
            }
          }
        }
      }
    }
    .our-mentors-block__title {
      padding-top: 150px;
    }
    .our-mentors-block__slider {
      display: flex;
      justify-content: center;
      align-items: center;
      padding: 30px 0px;
      text-align: center;
      .our-mentors-block__slide {
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: center;
        img {
          margin-right: 150px;
        }
        .our-mentors-block__about-mentor {
          text-align: left;
          .our-mentors-block__name-mentors {
            color: $blue;
            font-weight: 700;
            padding-bottom: 50px;
            font-size: 20px;
          }
          .our-mentors-block__info {
            margin: 15px 0px;
          }
        }
      }
    }
  }
}

@media (max-width: 500px) {
  img{
    width: 200px;
    height: auto;
  }
}
/* For screens with width less than 880px*/
@media (max-width: 600px) {
  /* CSS rules for responsive design */
  .our-mentors-block {
    margin-bottom: 100px;
    .container {
      .our-mentors-block__title {
        padding-top: 90px;
      }
      .our-mentors-block__slider {
        display: flex;
        justify-content: center;
        align-items: center;
        padding: 30px 0px;
        text-align: center;
        .our-mentors-block__slide {
          display: flex;
          flex-direction: column;
          align-items: center;
          justify-content: center;
          img {
            margin-right: 0px;
            max-width: 100%;
          }
          .our-mentors-block__about-mentor {
            text-align: center;
            .our-mentors-block__name-mentors {
              color: $blue;
              font-weight: 700;
              padding-bottom: 0px;
              padding-top: 30px;
              font-size: 22px;
            }
            .our-mentors-block__info {
              margin: 15px 0px;
              font-size: 20px;
              line-height: 25px;
            }
          }
        }
      }
    }
  }
}

/* For screens with width from 600px to 900px */
@media (min-width: 600px) and (max-width: 900px) {
  /* CSS rules for responsive design */
  .our-mentors-block {
    margin-bottom: 100px;
    .container {
      .our-mentors-block__title {
        padding-top: 90px;
      }
      .our-mentors-block__slider {
        display: flex;
        justify-content: center;
        align-items: center;
        padding: 30px 0px;
        text-align: center;
        .our-mentors-block__slide {
          display: flex;
          flex-direction: row;
          align-items: center;
          justify-content: center;
          img {
            margin-right: 50px;
            max-width: 100%;
          }
          .our-mentors-block__about-mentor {
            text-align: left;
            .our-mentors-block__name-mentors {
              color: $blue;
              font-weight: 700;
              padding-bottom: 0px;
              padding-top: 30px;
              font-size: 22px;
            }
            .our-mentors-block__info {
              margin: 15px 0px;
              font-size: 18px;
              line-height: 23px;
            }
          }
        }
      }
    }
  }
}
